@font-face {
  font-family: "Inter";
  src: url(/react-around-auth/static/media/Inter-Regular.3ac83020.woff) format("woff"),
    url(/react-around-auth/static/media/Inter-Regular.dc131113.woff2) format("woff2");
  font-weight: 400;
}

@font-face {
  font-family: "Inter";
  src: url(/react-around-auth/static/media/Inter-Medium.c0638bea.woff) format("woff"),
    url(/react-around-auth/static/media/Inter-Medium.c0638bea.woff) format("woff2");
  font-weight: 500;
}

@font-face {
  font-family: "Inter";
  src: url(/react-around-auth/static/media/Inter-Black.d0b121f3.woff) format("woff"),
    url(/react-around-auth/static/media/Inter-Black.661569af.woff2) format("woff2");
  font-weight: 900;
}

.body {
  background-color: #000;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
  font-family: 'Inter';
  font-style: normal;
}

.header {
  max-width: 880px;
  margin: 45px auto 41px;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__links {
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
  grid-gap: 24px;
  gap: 24px;
  padding: 0;
  list-style: none;
  color: white;
}

.header__links_mobile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  width: 100%;
}

.header__link {
  color: white;
  text-decoration: none;
  padding: 0;
  transition: opacity 0.3s ease-in-out;
}

.header__link:hover {
  opacity: 0.6;
}

.header__menu-button {
  background: transparent;
  border: none;
  display: none;
  cursor: pointer;
}

.header__mobile-menu {
  display: none;
}

.header__mobile-menu_opened {
  display: flex;
  flex-direction: column;
}

.header__close-icon {
  width: 25px;
}

@media screen and (max-width: 768px) {
  .header__menu-button {
    display: block;
  }

  .header__links_desktop {
    display: none;
  }

  .header__links_signup-login-page {
    display: block;
  }
}

@media screen and (max-width: 320px) {
  .header {
    margin: 28px auto 32px;
  }
}

.logo {
  max-width: 173px;
  max-height: 33px;
}

@media screen and (max-width: 320px) {
  .logo {
    max-width: 126px;
    max-height: 24px;
    margin-left: calc(27px - 19px);
  }
}

.content {
  max-width: 880px;
  margin: 0 auto;
  font-family: "Inter", Arial, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.22;
}

.separator {
  border-bottom: #545454 solid 1px;
  width: 100%;
  opacity: 0.7;
  margin-top: 36px;
}

.separator_type_mobile-menu {
  margin-top: none;
  margin-bottom: 36px;
}

.profile {
  display: flex;
  color: #fff;
  width: 100%;
  justify-content: space-between;
  grid-gap: 30px;
  gap: 30px;
  align-items: center;
  margin-bottom: 46px;
}

.profile__image {
  border-radius: 50%;
  max-width: 120px;
  max-height: 120px;
  position: relative;
  z-index: 1;
  height: 100%;
  width: 100%;
  object-fit: cover;
  /* visibility: hidden; */
}

.profile__image-container {
  position: relative;
  border-radius: 100px;
  height: 120px;
  width: 120px;
}

.profile__image-container::before {
  cursor: pointer;
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  border-radius: 100px;
  height: 100%;
  z-index: 5;
  transition: background-color 0.4s ease-out;
}

.profile__image-container:hover::before {
  background-color: rgba(0, 0, 0, 0.8);
  background-image: url(/react-around-auth/static/media/edit_icon.6bfdb354.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px;
}

.profile__info {
  flex-grow: 1;
}

.profile__person {
  display: flex;
  grid-gap: 18px;
  gap: 18px;
  align-items: flex-end;
}

.profile__name {
  padding: 0;
  margin: 0;
  font-size: 42px;
  line-height: 1.14;
  font-weight: 500;
}

.profile__title {
  padding: 0;
  margin: 9px 0 0;
}

.profile__add-button {
  background-color: transparent;
  background-image: url(/react-around-auth/static/media/plus_icon.b2fce4ac.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 22px 22px;
  padding: calc(12px + (22px / 2)) calc(62px + (22px / 2));
  border: solid 2px #fff;
  border-radius: 2px;
  box-sizing: border-box;
  transition: all 0.2s ease-out;
  cursor: pointer;
}

.profile__add-button:hover {
  opacity: 0.6;
}

.profile__edit-button {
  background-color: transparent;
  background-image: url(/react-around-auth/static/media/edit_icon.6bfdb354.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 10px 10px;
  padding: calc(9px + (10px / 4));
  border: #fff solid 1px;
  box-sizing: border-box;
  margin-bottom: 8px;
  transition: all 0.2s ease-out;
  cursor: pointer;
  max-height: 24px;
  max-width: 24px;
}

.profile__edit-button:hover {
  opacity: 0.6;
}

@media screen and (max-width: 765px) {
  .profile__name {
    font-size: 28px;
    line-height: 1;
  }
  .profile__edit-button {
    margin-bottom: 2px;
  }
}

@media screen and (max-width: 630px) {
  .profile {
    flex-direction: column;
    text-align: center;
  }
}

@media screen and (max-width: 400px) {
  .profile__add-button {
    width: 100%;
    margin-top: 0px;
  }
}

@media screen and (max-width: 320px) {
  .profile {
    margin-bottom: 36px;
    grid-gap: 33px;
    gap: 33px;
  }
  .profile__person {
    grid-gap: 9px;
    gap: 9px;
  }
  .profile__name {
    font-size: 22px;
    line-height: 1.2;
  }

  .profile__title {
    font-size: 14px;
    line-height: 1.21;
  }

  .profile__edit-button {
    background-size: 7px 7px;
    padding: calc(6px + (10px / 4));
    margin-bottom: 4px;
  }
}

.cards__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, 282px);
  justify-content: center;
  grid-gap: 20px 17px;
  gap: 20px 17px;
  padding: 0;
  list-style: none;
}

.card {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  position: relative;
}

.card__delete-button {
  position: absolute;
  top: 18px;
  right: 15px;
  cursor: pointer;
  background-color: transparent;
  background-image: url(/react-around-auth/static/media/trash.7ee866f1.svg);
  min-height: 19px;
  min-width: 18px;
  background-position: center;
  background-size: cover;
  border: none;
}

.card__image {
  cursor: pointer;
  background-color: white;
  width: 100%;
  display: block;
  border-radius: 10px 10px 0 0;
  min-height: 282px;
  max-height: 282px;
  object-fit: cover;
}

.card__title-area {
  background-color: white;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  display: flex;
  grid-gap: 8px;
  gap: 8px;
  justify-content: center;
  align-items: center;
  padding: 25px 15px 24px 21px;
  border-radius: 0 0 10px 10px;
}

.card__title {
  margin: 0;
  padding: 0;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  flex-grow: 1;
  font-weight: 900;
  font-size: 24px;
  line-height: 1.2;
}

.card__like-button {
  background-color: transparent;
  border: none;
  padding: 0;
  background-image: url(/react-around-auth/static/media/heart_icon.dd31dd06.svg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-width: 21px;
  min-height: 19px;
  transition: all 0.2s ease-out;
  cursor: pointer;
}

.card__like-button:hover {
  opacity: 0.5;
}

.card__like-button_active {
  background-image: url(/react-around-auth/static/media/heart_icon-filled.ef38b085.svg);
}

.card__like-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 4px;
  gap: 4px;
}

.form {
  max-width: 358px;
  width: 358px;
  display: flex;
  flex-direction: column;
  grid-gap: 32px;
  gap: 32px;
  font-size: 14px;
  line-height: 1.21;
}

.form__fieldset {
  display: flex;
  flex-direction: column;
  border: none;
  padding: 0;
}

.form__input {
  padding: 13px 7px;
  border-top: 0;
  border-left: 0;
  border-right: 0;
  border-bottom: rgba(0, 0, 0, 0.2) 1px solid;
}

.form__input::-webkit-input-placeholder {
  color: #000;
}

.form__input::placeholder {
  color: #000;
}

.form__input:focus {
  outline: none;
}

.form__input-error {
  font-size: 12px;
  padding-top: 4px;
  min-height: 16px;
  color: #ff0000;
}

.form__input_type_error {
  border-bottom: solid #ff0000 1px;
}

.form__input-container {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
}

.form__button {
  background: #000;
  border: none;
  color: #fff;
  font-size: 18px;
  border-radius: 2px;
  line-height: 1.22;
  padding: 18.49px;
  cursor: pointer;
  transition: background ease-out 0.3s;
}

.form__button:hover {
  background: rgba(0, 0, 0, 0.8);
}

.form__button_disabled,
.form__button_disabled:hover {
  background-color: white;
  border: 1px solid rgba(0, 0, 0, 0.2);
  color: rgba(0, 0, 0, 0.2);
  cursor: default;
}

.form__button_type_tooltip {
  display: none;
}

@media screen and (max-width: 524px) {
  .form {
    width: 100%;
    max-width: none;
  }
}

.footer {
  font-family: "Inter", Arial, sans-serif;
  max-width: 880px;
  color: #545454;
  margin: 63px auto 60px;
}

.footer__copyrights {
  padding: 0;
  margin: 0;
  font-size: 18px;
  line-height: 1.2;
}

@media screen and (max-width: 630px) {
  .footer__copyrights {
    text-align: center;
  }
}

@media screen and (max-width: 320px) {
  .footer__copyrights {
    text-align: left;
    font-size: 14px;
    line-height: 1.21;
  }
}

@media screen and (max-width: 320px) {
  .footer {
    margin: 50px auto 36px;
  }
}

.popup {
  position: fixed;
  /* min-width: 400px; */
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
  font-family: 'Inter', Arial, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.22;
  display: flex;
  visibility: hidden;
  z-index: 10;
  opacity: 0;
  transition: all 0.45s ease-in-out;
}

.popup_type_tooltip {
  text-align: center;
}

.popup_opened {
  visibility: visible;
  opacity: 1;
}

.popup__container {
  background-color: white;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;
  padding: 34px;
  border-radius: 10px;
  position: relative;
}

.popup__container_type_tooltip {
  max-width: 430px;
  padding: 60px 36px;
}

.popup__close-button {
  position: absolute;
  top: -38px;
  right: -38px;
  cursor: pointer;
  transition: all 0.2s ease-out;
  background-color: transparent;
  background-image: url(/react-around-auth/static/media/close_icon.9293aa8f.svg);
  background-repeat: no-repeat;
  background-size: 30px 30px;
  background-position: center;
  border: none;
  padding: 20px;
}

.popup__close-button:hover {
  opacity: 0.6;
}

.popup__title {
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 1.2;
  font-weight: 900;
}
.popup__title_type_tooltip {
  font-size: 24px;
  font-weight: 900;
}
.popup__container_type_image-prev {
  padding: 0px;
  background-color: transparent;
  color: #fff;
  grid-gap: 10px;
  gap: 10px;
}

.popup__image {
  width: 100%;
  max-height: 75vh;
  max-width: 75vw;
}

.popup__caption {
  margin: 0;
  font-size: 12px;
  line-height: 15px;
}

@media screen and (max-width: 524px) {
  .popup {
    padding-left: 19px;
    padding-right: 19px;
    box-sizing: border-box;
  }
  .popup__container {
    width: 100%;
    grid-gap: 67px;
    gap: 67px;
    padding: 25px 22px;
  }

  .popup_type_tooltip .popup__container {
    padding: 50px 18px;
    grid-gap: 40px;
    gap: 40px;
  }

  .popup__container_type_tooltip {
    grid-gap: 0;
    gap: 0;
    padding-top: 50px;
  }
  .popup__close-button {
    background-size: 20px 20px;
    top: -49px;
    right: 7px;
  }

  .popup__close-button_type_tooltip {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    left: 50%;
    top: -66px;
    width: 30px;
  }

  .popup__container_type_image-prev {
    padding: 0;
    grid-gap: 10px;
    gap: 10px;
    max-width: 75vw;
  }
  .popup__close-button_type_image-prev {
    right: -38px;
    top: -38px;
  }
}

@media screen and (max-width: 920px) {
  .section {
    padding: 0 19px;
  }
}

.auth-form {
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 358px;
  margin: 0 auto;
  height: calc(100vh - 300px);
  padding-top: 20px;
}

.auth-form__title {
  font-size: 24px;
  line-height: 28px;
  font-weight: 900;
  padding: 0;
  margin: 0 0 37px 0;
}

.auth-form__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
}

.auth-form__input {
  width: 100%;
  height: 50px;
  border: none;
  border-bottom: 1px solid #fff;
  /* padding: 0 15px; */
  margin: 0 0 20px 0;
  font-size: 16px;
  line-height: 19px;
  font-weight: 400;
  color: #fff;
  background-color: transparent;
  box-sizing: border-box;
}

.auth-form__input::-webkit-input-placeholder {
  color: rgba(204, 204, 204, 1);
}

.auth-form__input::placeholder {
  color: rgba(204, 204, 204, 1);
}

.auth-form__input:focus {
  outline: none;
}

.auth-form__input:focus::-webkit-input-placeholder {
  color: transparent;
}

.auth-form__input:focus::placeholder {
  color: transparent;
}

.auth-form__footer {
  height: 100%;
  max-height: 400px;
  flex-grow: 1;
  display: flex;
  text-align: center;
  flex-direction: column-reverse;
  box-sizing: border-box;
  width: 100%;
}

.auth-form__submit-button {
  width: 100%;
  padding: 14px 84px;
  border: none;
  border-radius: 2px;
  background-color: #fff;
  color: #000;
  font-size: 18px;
  line-height: 24px;
  cursor: pointer;
  display: block;
}

.auth-form__footer-text {
  padding: 15px 0 0;
  margin: 0;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;

  color: #fff;
}

.auth-form__footer-link {
  text-decoration: none;
  color: #fff;
}

@media screen and (max-width: 600px) {
  .auth-form {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.tooltip__image {
  width: 120px;
  margin: 0 auto;
}

.tooltip__text {
  font-size: 24px;
  font-weight: 900;
  line-height: 30px;
  padding: 0;
  margin: 0;
}

.loader {
  margin: 0 auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 40vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}



